import Header from './Header';
import Footer from './Footer';
import AppSidebar from './AppSidebar'; // Assumes this will be created
import { SidebarInset } from '@/components/ui/sidebar';
import { getAllCategories, getAllTags } from '@/lib/markdown';

export default async function AppShell({ children }: { children: React.ReactNode }) {
  // Fetch categories and tags for the sidebar. This is a server component.
  const categories = await getAllCategories();
  const tags = await getAllTags();

  return (
    <div className="flex min-h-screen flex-col">
      <Header />
      <div className="flex flex-1">
        {/* AppSidebar is a client component, so data needs to be passed if fetched here */}
        <AppSidebar categories={categories} tags={tags} />
        <SidebarInset>
          <main className="flex-1 p-4 md:p-6 lg:p-8 animate-fade-in">
            {children}
          </main>
        </SidebarInset>
      </div>
      <Footer />
    </div>
  );
}
